<template>
  <div
    id="rect"
    ref="rectEl"
  >
    <div id="data">
      <slot name="data" />
    </div>
    <div id="description">
      <slot name="description" />
    </div>
  </div>
</template>

<script>
import { onMounted, ref } from 'vue'

export default {
  name: 'Rect',
  props: {
    color: {
      type: String,
      required: true
    }
  },
  setup (props) {
    const rectEl = ref(null)

    onMounted(() => {
      rectEl.value.style.color = props.color
      rectEl.value.style.backgroundColor = props.color + '55'
    })

    return {
      rectEl
    }
  }
}
</script>

<style lang="less" scoped>
#rect{
  border-radius: 0.3em;
  padding: 0.5em 1em;
  #data{
    font-weight: bold;
    font-size: 2em;
  }

  #description{

  }
}
</style>
